<template>
  <div>
    <div
      v-if="!unknown && !loading && !retake"
      class="pb-5 bg-gray-50 md:w-2/3 mx-auto rounded"
    >
      <div class="text-center pt-3">
        <h2 class="ma-auto my-4 text-2xl font-bold" v-if="result[1]">
          You Scored: {{ result[1] }}
        </h2>

        <div class="text-center mb-n5">
          <button
            class="mx-auto primary_btn my-2"
            v-if="this.ended < Date.now()"
            @click="solutionv = !solutionv"
          >
            {{ !solutionv ? "View" : "Hide" }} Solution
          </button>
          <button
            class="mx-auto my-2 primary_btn"
            v-else
            @click="endalert"
            v-text="'View Solution'"
          ></button>
        </div>
        <iframe
          v-if="solutionv"
          :src="`https://drive.google.com/file/d/${solutionpdf.substr(
            32,
            33
          )}/preview`"
          height="700"
          width="100%"
          class="my-1"
        ></iframe>
      </div>

      <div v-if="!retake && solution.length > 0" class="my-5 text-center">
        <rbtn
          :link="`/ranking/${this.$route.params.id}`"
          class="mx-auto my-2"
          text="View Your Rank"
        ></rbtn>
      </div>
      <div v-if="!retake && solution.length > 0" class="text-center">
        <button
          class="primary_btn mx-auto"
          @click="startRetake"
          v-text="'Test Yourself Again'"
        ></button>
      </div>
      <div
        class="text-center font-semibold my-4"
        v-if="!retake && solution.length > 0"
      >
        Retake exam score will not be recorded
      </div>
    </div>
    <div v-if="retake" class="pb-10">
      <div class="py-4 text-center">
        <h1 class="text-white text-2xl font-bold">
          Exam - {{ this.$route.params.id }}
        </h1>
      </div>
      <div class="md:w-2/3 md:mx-auto mx-2">
        <div
          v-for="(q, i) in solution"
          :key="i"
          class="bg-white mx-auto p-5 my-3 rounded-md"
        >
          <p v-html="q.question"></p>
          <div class="mt-3">
            <div
              v-html="q.a"
              v-if="q.a"
              @click="solution[i].selected = q.a"
              :class="`my-3 p-3 rounded  ${
                q.a == q.selected
                  ? 'border-2 border-green-500 bg-green-300'
                  : 'bg-gray-200'
              }`"
            ></div>
            <div
              v-html="q.b"
              v-if="q.b"
              @click="solution[i].selected = q.b"
              :class="`my-3 p-3 rounded  ${
                q.b == q.selected
                  ? 'border-2 border-green-500 bg-green-300'
                  : 'bg-gray-200'
              }`"
            ></div>
            <div
              v-html="q.c"
              v-if="q.c"
              @click="solution[i].selected = q.c"
              :class="`my-3 p-3 rounded  ${
                q.c == q.selected
                  ? 'border-2 border-green-500 bg-green-300'
                  : 'bg-gray-200'
              }`"
            ></div>
            <div
              v-html="q.d"
              v-if="q.d"
              @click="solution[i].selected = q.d"
              :class="`my-3 p-3 rounded  ${
                q.d == q.selected
                  ? 'border-2 border-green-500 bg-green-300'
                  : 'bg-gray-200'
              }`"
            ></div>
          </div>
        </div>
        <div class="text-center">
          <button
            @click="retakeResult"
            class="
              rounded
              py-3
              px-4
              shadow
              hover:shadow-lg
              bg-gradient-to-t
              from-green-500
              to-green-400
              mx-auto
              font-semibold
              text-white
              outline-none
            "
          >
            Submit
          </button>
        </div>
      </div>
    </div>
    <div v-if="questions.length > 0" class="pb-10">
      <div class="py-4 text-center">
        <h1 class="text-white text-2xl font-bold">
          Exam - {{ this.$route.params.id }}
        </h1>
      </div>
      <div class="md:w-2/3 md:mx-auto mx-2">
        <div
          v-for="(q, i) in questions"
          :key="i"
          class="bg-white mx-auto p-5 my-3 rounded-md bg-opacity-40"
        >
          <p
            class="flex flex-wrap kalpurush text-lg font-semibold mb-2"
            v-html="q.question"
          ></p>
          <div class="mt-3">
            <div
              v-html="q.a"
              v-if="q.a"
              @click="questions[i].selected = q.a"
              :class="`my-3 p-3 rounded  ${
                q.a == q.selected
                  ? 'border-2 border-green-500 bg-green-300'
                  : 'bg-gray-50'
              }`"
            ></div>
            <div
              v-html="q.b"
              v-if="q.b"
              @click="questions[i].selected = q.b"
              :class="`my-3 p-3 rounded  ${
                q.b == q.selected
                  ? 'border-2 border-green-500 bg-green-300'
                  : 'bg-gray-50'
              }`"
            ></div>
            <div
              v-html="q.c"
              v-if="q.c"
              @click="questions[i].selected = q.c"
              :class="`my-3 p-3 rounded  ${
                q.c == q.selected
                  ? 'border-2 border-green-500 bg-green-300'
                  : 'bg-gray-50'
              }`"
            ></div>
            <div
              v-html="q.d"
              v-if="q.d"
              @click="questions[i].selected = q.d"
              :class="`my-3 p-3 rounded  ${
                q.d == q.selected
                  ? 'border-2 border-green-500 bg-green-300'
                  : 'bg-gray-50'
              }`"
            ></div>
          </div>
        </div>
        <div class="text-center">
          <button
            @click="submitAns"
            :disabled="eload"
            class="
              rounded
              py-3
              px-4
              shadow
              hover:shadow-lg
              bg-gradient-to-t
              from-green-500
              to-green-400
              mx-auto
              font-semibold
              text-white
              outline-none
            "
          >
            {{ eload ? "Loading..." : "Submit" }}
          </button>
        </div>
      </div>
    </div>
    <div class="text-center my-10" v-if="loading">
      <loader />
    </div>
    <div class="white--text fixed__timer" v-if="show">
      <div class="bg-red-500 border border-white text-white">
        {{ minute }} : {{ second }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      timing: null,
      solutionv: false,
      retake: false,
      questions: [],
      solution: [],
      result: [],
      unknown: false,
      loading: true,
      eload: false,
      minute: 0,
      second: 0,
      end: Date.now() + 12000000,
      show: false,
      url: "",
      solutionpdf: "",
      starttime: Date.now(),
      ended: null,
      inttime: 109090,
    };
  },
  methods: {
    endalert() {
      this.$swal({
        icon: "warning",
        title: "Will be available after the time ends",
      });
    },
    getQuestionLink() {
      fetch(
        `https://script.google.com/macros/s/AKfycbwvwOv6OKjK8qTJdFcQF4v_AthQddTji1NEJhinBkD1XfvDggrYW8Dn4rBsXNNne61ktg/exec?id=${this.$route.params.id}`
      )
        .then((res) => res.json())
        .then((data) => {
          this.url = data.exam.question;
          this.inttime = data.exam.time * 60000;
          this.end = data.exam.time * 60000;
          this.ended = new Date(data.exam.end).getTime();

          this.solutionpdf = data.exam.solve_sheet;
          let phone = localStorage.getItem("acuser22");
          fetch(this.url + "?type=check&&phone=" + phone)
            .then((res) => res.json())
            .then((data) => {
              if (data.message == "exists" || this.ended < Date.now()) {
                this.result = data.result ? data.result : "";
                fetch(this.url + "?type=question")
                  .then((r) => r.json())
                  .then((d) => {
                    this.solution = d;
                  });

                this.loading = false;
              } else {
                this.unknown = true;
                fetch(this.url + "?type=question")
                  .then((r) => r.json())
                  .then((d) => {
                    this.questions = d.map((m) => {
                      return { ...m, selected: "" };
                    });
                    this.loading = false;
                    this.showRemaining(this.end + Date.now());
                    this.autoSubmit();
                  });
              }
            });
        });
    },
    startRetake() {
      this.retake = true;
      this.showRemaining(Date.now() + 60000 * this.end);
    },
    retakeResult() {
      let score = this.solution.reduce((a, c) => {
        if (c.correct == c.selected) {
          a += 10;
        }
        return a;
      }, 0);
      this.$swal({
        icon: "success",
        title: "Success",
        text: `You Scored ${score}`,
      }).then(() => {
        this.retake = false;
      });
    },
    submitAns() {
      this.eload = true;
      let score = this.questions.reduce((a, c) => {
        if (c.correct == c.selected) {
          a += 10;
        }
        return a;
      }, 0);
      let submitAns = localStorage.getItem(`examm${this.$route.params.id}`);
      if (!submitAns) {
        let phone = localStorage.getItem("acuser22");
        fetch(this.url, {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify({
            type: "mcq",
            mcq: [phone, score, Date.now() - this.starttime],
          }),
        }).then(() => {
          localStorage.setItem(`examm${this.$route.params.id}`, "submited");
          clearTimeout(this.timing);
          this.eload = false;
          this.$swal({
            icon: "success",
            title: "Success",
            text: `You Scored ${score}`,
          }).then(() => {
            this.$router.replace("/ranking/" + this.$route.params.id);
          });
        });
      } else {
        this.$router.replace("/ranking/" + this.$route.params.id);
      }
    },
    autoSubmit() {
      this.timing = setTimeout(() => {
        this.show = false;

        this.submitAns();
      }, this.inttime);
    },
    showRemaining(end) {
      this.show = true;
      const timer = setInterval(() => {
        const distance = end - Date.now();
        if (distance < 0) {
          clearInterval(timer);
          return;
        }
        const days = Math.floor(distance / this._days);
        const hours = Math.floor((distance % this._days) / this._hours);
        const minutes = Math.floor((distance % this._hours) / this._minutes);
        const seconds = Math.floor((distance % this._minutes) / this._seconds);
        this.second = seconds < 10 ? "0" + seconds : seconds;
        this.minute = minutes < 10 ? "0" + minutes : minutes;
        this.hours = hours < 10 ? "0" + hours : hours;
        this.days = days < 10 ? "0" + days : days;
      }, 1000);
    },
  },

  created() {
    this.getQuestionLink();
  },
  computed: {
    _seconds: () => 1000,
    _minutes() {
      return this._seconds * 60;
    },
    _hours() {
      return this._minutes * 60;
    },
    _days() {
      return this._hours * 24;
    },
    user() {
      return this.$store.getters.getUser;
    },
  },
};
</script>

<style lang="scss" scoped>
.fixed__timer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  color: white;
  font-size: 22px;
  position: fixed;
  bottom: 0;
}
.fixed__timer > div {
  font-size: 20px;

  padding: 10px;

  border-radius: 5px;
}
.solution {
  border: 1px solid rgba(black, 0.2);
  border-radius: 4px !important;
  background: linear-gradient(45deg, #f7f4f4, #d8d6d6b4) !important;
  box-shadow: 2px 2px 2px #c2c0c0, -2px -2px 4px #ffffff !important;
  div {
    color: black;
  }
  p {
    color: black;
  }
}
.examcontainer {
  background: rgba(255, 255, 255, 0.09) !important;
  backdrop-filter: blur(5px);
}
.examcontainerq {
  background: rgba(255, 255, 255, 0.774) !important;
  backdrop-filter: blur(5px);
}
</style>